header {
    position: fixed;
    width: 100%;
    height: 65px;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.5);
    overflow: hidden;
    z-index: 100;
    background-color: #f9f9f9;
}

.flex-row {
    height: 100%;
}

.header-brash {
    position: absolute;
    left: 0;
    height: 100%;
    z-index: -100;
}

.logo-app {
    margin-left: 70px;
    user-select: none;
}

nav {
    display: flex;
    align-items: center;
    justify-content: right;
}

nav ul {
    margin: 0;
    list-style: none;
    font-size: 25px;
    font-weight: 100;
}

nav ul li {
    margin-right: 75px;
    user-select: none;
}

nav ul li a {
    color: #000;
    text-decoration: none;
    padding: 18px 10px 13px 10px;
}

nav ul li a:hover {
    color: #000;
    text-decoration: none;
    border-bottom: 4px solid #000;
}