.service-wrapper {
    position: relative;
    width: 100%;
    min-height: 100vh;
    background-color: #f2f2f2;
}

.head-services {
    padding-top: 85px;
    color: #000;
    font-size: 46px;
    width: 700px;
    border-bottom: 2px solid #000;
    padding-bottom: 5px;
    margin-left: auto;
    margin-right: 70px;
    border-left: 50px solid transparent;
}

.back-swiper {
    position: absolute;
    width: 100%;
    height: 82%;
    z-index: 0;
}

.back-slide-swiper {
    background-repeat: no-repeat;
    animation: 25s infinite ease-in-out backswipe;
}

@keyframes backswipe {
    0% {
        background-position: left;
        background-size: 125%;
    }

    25% {
        background-size: 100%;
    }

    50% {
        background-position: right;
        background-size: 125%;
    }

    75% {
        background-size: 100%;
    }

    100% {
        background-position: left;
        background-size: 125%;
    }
}

.fir-sl {
    background-image: url("../Images/Services/backsw.jpg");
}

.sec-sl {
    background-image: url("../Images/Services/backsw1.jpg");
}

.thr-sl {
    background-image: url("../Images/Services/backsw2.jpg");
}

.service-swiper {
    position: absolute;
    width: 100%;
    height: 82%;
    z-index: 1;
    background-color: rgba(0, 0, 0, 0.5);
}

.card-wrapper {
    margin: 0 auto;
    min-height: 100%;
    width: 80%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
}

.card {
    width: 350px;
    height: 500px;
    box-shadow: 0px 1px 10px #000;
}

.top-card {
    position: relative;
    width: 100%;
    height: 50%;
}

.top-card img {
    position: absolute;
    width: 100%;
    height: auto;
    z-index: -1;
}

.name-pack {
    text-align: center;
    color: #fff;
    font-size: 36px;
    border-bottom: 2px solid #fff;
    width: 95%;
    margin: 0 auto;
    padding-top: 50px;
    padding-bottom: 5px;
}

.sale-price {
    text-align: center;
    color: #fff;
    font-size: 46px;
    margin-top: 10px;
    margin-bottom: 10px;
    font-weight: 100;
}

.old-price {
    text-align: center;
    color: #fff;
    font-size: 36px;
    font-weight: 100;
    text-decoration: line-through;
}

.bottom-card {
    width: 100%;
    height: 50%;
    background-color: #ffffff;
}

.bottom-card p {
    text-align: justify;
    font-size: 22px;
    padding: 20px 20px;
    margin: 0;
}

.button-card {
    margin: 0 auto;
    border: 1px solid #000;
    width: 60%;
    height: 40px;
}

.button-card a {
    width: 100%;
    height: 100%;
    text-decoration: none;
    color: #000;
    font-size: 26px;
    font-weight: 100;
    transition: 0.4s;
}

.button-card a:hover {
    text-decoration: none;
    color: #fff;
    background-color: rgba(0, 0, 0, 0.8);
}