.box-slider {
    position: relative;
    width: 100%;
    height: 100vh;
}

.mySlider {
    width: 100%;
    height: 100%;
}

.swiper-button-prev, .swiper-button-next {
    color: #fff;
}

.swiper-button-prev {
    margin-left: 70px;
}

.swiper-button-next {
    margin-right: 70px;
}

.mySwiperSlide1 {
    background-image: url("../Images/Slider/slide1.jpg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.content-slide1 {
    padding-top: 200px;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
}

.tWelcome {
    color: #fff;
    font-size: 62px;
    font-weight: 300;
    user-select: none;
    margin-bottom: 85px;
}

.myLogo {
    display: block;
    margin: 0 auto;
}

.serviceBtn {
    width: 450px;
    border: 2px solid #fff;
    border-radius: 5px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 150px;
}

.serviceBtn a {
    text-align: center;
    display: block;
    width: 100%;
    height: 100%;
    font-size: 45px;
    font-weight: 100;
    color: #fff;
    padding: 5px 0;
    text-decoration: none;
    transition: 0.5s;
}

.serviceBtn a:hover {
    color: #fff;
    text-decoration: none;
    background-color: rgba(255, 255, 255, 0.4);
}

