.about-wrapper {
    width: 100%;
    min-height: 100vh;
    background: linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(242,242,242,1) 100%);
    overflow: hidden;
}

.about-head {
    padding-top: 125px;
    color: #000;
    font-size: 46px;
    width: 700px;
    border-bottom: 2px solid #000;
    padding-bottom: 5px;
    margin-left: 70px;
    border-right: 50px solid transparent;
}

.img-block img {
    max-width: 100%;
    height: auto;
    user-select: none;
    pointer-events: none;
}

.abtxt-wrapper {
    position: relative;
}

.abtxt-wrapper p {
    padding: 10px;
    width: 600px;
    font-size: 36px;
    font-weight: 100;
    text-align: justify;
    margin-bottom: 0;
    user-select: none;
}

.borders {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 120px;
    height: 150px;
    border-left: 1px solid #000;
    border-bottom: 1px solid #000;
}